.App {
  padding-top: 0;
  margin: 0 auto;
  height: 100%;
  min-width: 375px;
  max-width: 900px;
  min-height: 850px;
  background: #292929; /* fallback for old browsers */
  color: #e2e2e2;
  background-attachment: fixed;
}
@media (max-width: 450px) {
  select {
    font-size: small;
    width: 60px;
  }
}
@media (min-width: 450px) {
  select {
    font-size: small;
    width: 80px;
  }
}
@media (min-width: 670px) {
  select {
    font-size: medium;
    width: 100px;
  }
}

h1 {
  margin: 0 10px;
  height: 100%;
  color: #f7f7f7;
}
.selectBox {
  max-width: 900px;
  width: 100%;
  padding: 10px 0;
  padding-top: 10px;
  position: fixed;
  z-index: 1;
  top: 70;
  background: #757575d0;
}
.card_list {
  padding-top: 75px;
  width: 100%;
}
.selectBox,
.card_list {
  text-align: center;
  flex-direction: row;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
select {
  font-weight: bold;
  height: 43px;
  background-color: #504e4b53;
  border: none;
  border-radius: 15px;
  margin: 2px 0;
  display: flex;
  color: #e2e2e2;
  text-align: center;
}
select option[value=""][disabled] {
  display: none;
}
a {
  text-decoration: none;
}
.img {
  width: 90%;
  min-width: 80px;
  max-width: 120px;
  min-height: 80px;
  max-height: 100px;
  height: auto;
  border-radius: 10px;
  margin: 0 auto;
}
.title {
  display: inline;
  font-size: 12px;
  margin: 0;
  color: #c7c5c5;
}
.theme {
  font-size: 8px;
}
.dong,
.gu_si,
.theme {
  margin: 0;
}
.card {
  background-color: #868686d9;
  border: 2px solid #979797;
  border-radius: 20px;
  padding: 6px 5px;
  margin: 6px;
  cursor: pointer;
  text-align: center;
}
h6 {
  color: #e2e2e2;
  font-size: 8px;
  margin-right: 5px;
  display: inline;
}
h5 {
  font-weight: 100;
  margin: 0;
  font-size: 7px;
  color: #e2e2e2;
}
.bottom {
  bottom: 0;
  position: fixed;
  background: #868686d9;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  height: 25px;
  text-align: center;
  color: #e2e2e2;
}
.link {
  text-decoration: none;
  margin: 0 30px;
}
.scroll__container {
  position: fixed;
  right: 5%;
  bottom: 5%;
  z-index: 1;
}
.top {
  font-weight: bold;
  font-size: 15px;
  padding: 15px 10px;
  background-color: #9b9b9bd9;
  color: #fff;
  border: 1px solid rgb(210, 204, 193);
  border-radius: 50%;
  outline: none;
  cursor: pointer;
}
.top:hover {
  color: #e2e2e2;
}
